header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header_container{
    text-align: center;
    height: 100%;
    margin-top: 2.5rem;
    position: relative;
}


.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 5rem;
    
}
 



.me{
    background: linear-gradient(var(--color-primary),transparent);
    width: 20rem;
    height: 21.8rem;
    position: relative;
    left: calc(50% - 10rem);
    margin-top: 2rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;   
} 

.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 8rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}   


@media screen and (max-width:1024px){
    header{
        height: 68vh;
    }
}

@media only screen and (min-width: 200px) and (max-width:767px){
    header{
        height: 100vh;
    }
    
    .scroll_down{
        display: none;
    }

}